import { Component } from "react";
import image from "../../img/choose_wisely.jpg";
import redImg from "../../img/red.jpg";
import blueImg from "../../img/blue.jpg";
import oracle from "../../img/oracle.jpg";
import Answer from "./Answer/index";

import { Container, Header, Image, Options, Option, Button } from "./styled";

const red = [1, 2, 4, 7];
const blue = [3, 5, 6, 8];

class ChooseWisely extends Component {
  constructor(props) {
    super(props);
    this.state = {
      chosen: [],
      answer: {},
      resolved: false,
      play: true,
    };
  }

  select = (idx) => {
    let array = this.state.chosen;
    if (array.includes(idx)) {
      let new_array = array.filter((el) => el !== idx);
      array = new_array;
    } else {
      array.push(idx);
    }
    this.setState({
      chosen: array,
      disabled: false,
    });
  };

  close = () => {
    this.setState({
      resolved: false,
    });
  };

  resolve = () => {
    let { chosen } = this.state;
    chosen.sort();
    let resultRed = true;
    let resultBlue = true;

    for (let i = 0; i < chosen.length; i++) {
      if (chosen[i] !== red[i]) {
        resultRed = false;
      }
      if (chosen[i] !== blue[i]) {
        resultBlue = false;
      }
    }

    let props;

    if (resultRed && chosen.length === 4) {
      props = {
        text: "You have chosen wisely, but you must solve another puzzle",
        button: "Continue",
        image: redImg,
        action: this.props.action,
      };
    }
    if (resultBlue && chosen.length === 4) {
      props = {
        text:
          "Do you really want to ignore your birthday and forget about gifts?",
        button: "NOOOOOOOO!",
        image: blueImg,
        action: this.close,
      };
    }
    if ((!resultRed && !resultBlue) || chosen.length < 4) {
      props = {
        text: "Try again or ask The Oracle for some hints",
        button: "OK",
        image: oracle,
        action: this.close,
      };
    }

    this.setState({
      answer: props,
      resolved: true,
      play: false,
    });
  };

  render() {
    const { chosen, resolved, answer } = this.state;
    return (
      <Container>
        <Header>
          <Image src={image} />
        </Header>
        <Options>
          {Array.from(Array(8).keys()).map((opt, id) => {
            let idx = id + 1;
            return (
              <Option
                id={idx}
                onClick={(e, id) => {
                  this.select(idx);
                }}
                key={idx}
                selected={chosen.includes(idx)}
              >
                <img src={require(`../../img/options/option${idx}.jpg`)} />
              </Option>
            );
          })}
        </Options>
        <Button onClick={this.resolve} />
        {resolved && <Answer {...answer} />}
      </Container>
    );
  }
}

export default ChooseWisely;
