import { Component } from "react";
import {
  Container,
  Boxes,
  Box,
  Wrapper,
  Season,
  DragTo,
  Evan,
  Evans,
  Button,
  TryAgain,
} from "./styled";
import Scare from "./Scare";
import Popup from "./Popup";

const evans = [
  {
    id: "james",
    img: require(`../../img/evans/evan5.jpg`),
  },
  {
    id: "edward",
    img: require(`../../img/evans/evan6.jpg`),
  },
  {
    id: "tate",
    img: require(`../../img/evans/evan1.jpg`),
  },
  {
    id: "trevor",
    img: require(`../../img/evans/evan9.jpg`),
  },
  {
    id: "austin",
    img: require(`../../img/evans/evan10.jpg`),
  },
  {
    id: "kit",
    img: require(`../../img/evans/evan2.jpg`),
  },
  {
    id: "jeff",
    img: require(`../../img/evans/evan8.jpg`),
  },
  {
    id: "jimmy",
    img: require(`../../img/evans/evan4.jpg`),
  },
  {
    id: "kai",
    img: require(`../../img/evans/evan7.jpg`),
  },
  {
    id: "kyle",
    img: require(`../../img/evans/evan3.jpg`),
  },
];

class AmericanHorrorStory extends Component {
  constructor(props) {
    super(props);
    this.state = {
      s1: "",
      s2: "",
      s3: "",
      s4: "",
      s5: "",
      s6: "",
      s7: "",
      s8: "",
      s9: "",
      s10: "",
      resolved: false,
      correct: false,
      scare: false,
      idx: 0,
      input: "",
      animate: true,
    };
  }

  handleChange = (e) => {
    this.setState({
      input: e.target.value,
    });
  };

  handleAnswer = () => {
    const currentValue = this.state.input.toLowerCase().replace(/ /g, "");
    if (currentValue === "happybirthday!") {
      this.props.action();
    } else {
      this.setState({
        animate: true,
      });
    }
  };

  finishAnimation = () => {
    this.setState({
      animate: false,
    });
  };

  dragStart = (event) => {
    event.dataTransfer.setData("text", event.target.src);
    event.dataTransfer.setData(
      "answer",
      event.target.getAttribute("data-season")
    );
    event.stopPropagation();
  };
  dragOver = (event) => {
    event.preventDefault();
  };
  dragEnd = (event) => {
    event.stopPropagation();
  };

  handleDrop = (event) => {
    let data = event.dataTransfer.getData("text");
    let answer = event.dataTransfer.getData("answer");
    event.target.setAttribute("src", data);
    let name = `s${event.target.id}`;
    this.setState({
      [name]: answer,
      scare: true,
      idx: event.target.id - 1,
    });
  };

  resolve = () => {
    const { s1, s2, s3, s4, s5, s6, s7, s8, s9, s10 } = this.state;
    let answer = true;
    if (s1 !== "tate") answer = false;
    if (s2 !== "kit") answer = false;
    if (s3 !== "kyle") answer = false;
    if (s4 !== "jimmy") answer = false;
    if (s5 !== "james") answer = false;
    if (s6 !== "edward") answer = false;
    if (s7 !== "kai") answer = false;
    if (s8 !== "jeff") answer = false;
    if (s9 !== "trevor") answer = false;
    if (s10 !== "austin") answer = false;

    this.setState({
      resolved: true,
      correct: answer,
    });
  };

  hideScare = () => {
    this.setState({
      scare: false,
    });
  };

  hideTryAgain = () => {
    this.setState({
      resolved: false,
    });
  };

  render() {
    const { resolved, correct, scare, idx } = this.state;
    return (
      <Container>
        {scare && <Scare index={idx} end={this.hideScare} />}
        <Wrapper>
          <Boxes>
            {Array.from(Array(10).keys()).map((opt, id) => {
              let idx = id + 1;
              return (
                <Box key={idx}>
                  <Season src={require(`../../img/seasons/season${idx}.jpg`)} />
                  <DragTo
                    id={idx}
                    droppable
                    onDrop={this.handleDrop}
                    onDragOver={this.dragOver}
                    draggable={false}
                  />
                </Box>
              );
            })}
          </Boxes>
          <Evans>
            {evans.map((e) => {
              return (
                <Evan
                  key={e.id}
                  draggable
                  onDragStart={this.dragStart}
                  onDragEnd={this.dragEnd}
                  src={e.img}
                  data-season={e.id}
                />
              );
            })}
          </Evans>
          <Button onClick={this.resolve} />
          <div style={{ color: "white" }}>
            {resolved && correct && (
              <Popup
                handleChange={this.handleChange}
                value={this.state.input}
                check={this.handleAnswer}
                animate={this.state.animate}
                onAnimationEnd={this.finishAnimation}
              />
            )}

            {resolved && !correct && (
              <TryAgain onAnimationEnd={this.hideTryAgain} />
            )}
          </div>
        </Wrapper>
      </Container>
    );
  }
}

export default AmericanHorrorStory;
