import { Component } from "react";
import "./App.css";
import ChooseWisely from "./riddles/ChooseWisely";
import AmericanHorrorStory from "./riddles/AmericanHorrorStory";
import GuessTheMovie from "./riddles/GuessTheMovie";

const riddles = ["chooseWisely", "americanHorrorStory", "guessTheMovie"];

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      riddle: 2,
    };
  }

  nextRiddle = () => {
    let { riddle } = this.state;

    this.setState({
      riddle: riddle + 1,
    });
  };

  const;
  render() {
    const { riddle } = this.state;
    return (
      <div className="App">
        {riddles[riddle] === "chooseWisely" && (
          <ChooseWisely action={this.nextRiddle} />
        )}
        {riddles[riddle] === "americanHorrorStory" && (
          <AmericanHorrorStory action={this.nextRiddle} />
        )}
        {riddles[riddle] === "guessTheMovie" && (
          <GuessTheMovie action={this.nextRiddle} />
        )}
      </div>
    );
  }
}

export default App;
