import styled, { keyframes, css } from "styled-components";
import cinema from "../../img/cinema.jpg";
import movie from "../../img/movie.png";
import movieFrame from "../../img/movie_frame.png";
import bulb from "../../img/lightbulb.png";
import { fadeIn, tada, bounceInDown, wobble } from "react-animations";

export const Container = styled.div`
  height: 100vh;
  background-image: url(${cinema});
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: relative;
`;

const blink = keyframes`
  from {
    opacity: 0.5;
    background-color: red
  }

  to {
    opacity: 0;
    background-color: white;
  }
`;

export const Wrong = styled.div`
  animation: ${blink} 1s linear;
  animation-timing-function: ease-out;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 90px;
  z-index: 10;
`;
const wobbleAnimation = keyframes`${wobble}`;

export const Riddle = styled.div`
  margin: 30px;
  background-image: url(${movie});
  width: 960px;
  height: 558px;
  padding: 50px;
  margin-top: 75px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  animation: ${(props) => (props.animate ? css`1s ${wobbleAnimation}` : css``)};
`;

export const Title = styled.img`
  position: absolute;
  top: 50px;
  left: 50%;
  transform: translateX(-50%);
`;

export const Audio = styled.audio`
  margin-top: 70px;
  margin-bottom: 50px;
`;

export const Label = styled.img`
  margin-bottom: 20px;
  width: 250px;
  height: auto;
  position: relative;
  z-index: 3;
`;

export const Input = styled.input`
  height: 60px;
  width: 250px;
  margin-bottom: 50px;
  outline: none !important;
  border-radius: 15px;
  border: 4px solid #c00000;
  text-align: center;
  color: #c00000;
  font-size: 30px;
  position: relative;
  z-index: 3;
`;

export const ButtonContainer = styled.div`
  height: 60px;
  width: 250px;
  border-radius: 15px;
  margin-bottom: 30px;
  background-image: url(${(props) => props.bg});
  background-size: 200px 30px;
  background-repeat: no-repeat;
  background-color: #feaf64;
  background-position: center;
  border-color: #c00000;
  cursor: pointer;
  transition: all 0.25s ease-out;
  position: relative;
  z-index: 3;

  &:hover {
    opacity: 0.8;
  }
`;

export const Button = styled.button`
  height: 60px;
  width: 250px;
  background-image: url(${movieFrame});
  background-size: 250px 65px;
  background-position: center;
  background-color: transparent;
  background-repeat: no-repeat;
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 15px;
  position: relative;
  z-index: 3;

  &:hover {
    opacity: 0.8;
  }
`;

const shake = keyframes`
  1%, 9% {
    transform: translate3d(-1px, 0, 0);
  }
  
  2%, 8% {
    transform: translate3d(2px, 0, 0);
  }

  3%, 5%, 7% {
    transform: translate3d(-4px, 0, 0);
  }

  4%, 6% {
    transform: translate3d(4px, 0, 0);
  }
`;

export const Bulb = styled.div`
  height: 120px;
  width: 120px;
  background-image: url(${bulb});
  position: absolute;
  bottom: 50px;
  right: 50px;
  background-size: 100px 100px;
  border: 4px solid #c00000;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  animation: ${shake} 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  transform: translate3d(0, 0, 0);
  backface-visibility: hidden;
  perspective: 1000px;
  animation-delay: 5s;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  transition: all 0.25s ease;

  &:hover {
    opacity: 0.6;
  }
`;

export const Hint = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
`;

const fadeAnimation = keyframes`${fadeIn}`;

export const Background = styled.div`
  background-image: url(${(props) => props.image});
  position: absolute;
  top: 0;
  left: 0;
  height: calc(100% - 56px);
  width: calc(100% - 56px);
  margin: 28px;
  border-radius: 90px;
  animation: 2s ${fadeAnimation};
  background-repeat: no-repeat;
  background-size: cover;
`;

const tadaAnimation = keyframes`${tada}`;

export const Quote = styled.img`
  width: calc(100% - 140px);
  height: auto;
  top: 50px;
  margin: auto;
  position: relative;
  z-index: 2;
  animation: ${tadaAnimation} 0.5s;
`;

const bounceInDownAnimation = keyframes`${bounceInDown}`;

export const Picture = styled.img`
  position: absolute;
  bottom: 56px;
  left: 50px;
  max-height: 370px;
  max-width: 370px;
  z-index: 2;
  animation: 0.5s ${bounceInDownAnimation};
`;
